// Gestione Password Caps Lock
function showCapsLockMsg($input) {
  $('.password-caps').remove();
  $input.parents('.form-group').append('<small class="password-caps form-text text-warning position-absolute bg-white w-100">CAPS LOCK inserito</small>');
}

$(function () {
  var isShiftPressed = false;
  var isCapsOn = null;

  $('.input-password')
    .on('keydown', function (e) {
      var keyCode = e.keyCode ? e.keyCode : e.which;
      if (keyCode == 16) {
        isShiftPressed = true;
      }
    })
    .on('keyup', function (e) {
      var keyCode = e.keyCode ? e.keyCode : e.which;
      if (keyCode == 16) {
        isShiftPressed = false;
      }
      if (keyCode == 20) {
        if (isCapsOn) {
          isCapsOn = false;
          $('.password-caps').remove();
        } else {
          isCapsOn = true;
          $('input:focus').each(function (e) {
            showCapsLockMsg($(this));
          });
        }
      }
    })
    .on('keypress', function (e) {
      var keyCode = e.keyCode ? e.keyCode : e.which;
      // i keyCode dal 65 al 90 identificano i caratteri alfabetici maiuscoli da "A" a "Z"
      if (keyCode >= 65 && keyCode <= 90 && !isShiftPressed) {
        isCapsOn = true;
        showCapsLockMsg($(this));
      }
    });

  $('.input-password-strength-meter').password();

  $('.password-icon').on('click', function (e) {
    $(this).find('[class^="password-icon"]').toggleClass('d-none');
    var input = $(this).siblings('.input-password');
    var inputType = (input.attr('type') === 'password') ? 'text' : 'password';
    input.attr('type', inputType);
  })
});

// open / close navbar actions
var openbutton = $('.custom-navbar-toggler');
var closebutton = $('.close-div ');
var overlay = $('.overlay');
var backbutton = $('.it-back-button');
var navlink = $('.navbar-collapsable a');

//-open button action
$(openbutton).on('click', function (event) {
  /* Act on the event */
  var target = $(this).attr('data-target');
  var fadelayer = $(target).find('.overlay');
  $(this).attr('aria-expanded', 'true');
  $(backbutton).fadeIn();
  $(target).show();
  $(fadelayer).fadeIn();
  $(target).addClass('expanded');
});

$(overlay).on('click', function () {
  var target = $(this).closest('.navbar-collapsable');
  var buttonrel = $(this).closest('.navbar').find(".custom-navbar-toggler");
  var fadelayer = $(target).find('.overlay');
  $(buttonrel).attr('aria-expanded', 'false');

  $(target).removeClass('expanded');
  $(fadelayer).fadeOut();
  setTimeout(function () {
    $(target).hide();
  }, 300);
});

//-close button action
$(closebutton).on('click', function (event) {
  var target = $(this).closest('.navbar-collapsable');
  var buttonrel = $(this).closest('.navbar').find(".custom-navbar-toggler");
  var fadelayer = $(target).find('.overlay');

  $(buttonrel).attr('aria-expanded', 'false');

  $(target).removeClass('expanded');
  $(fadelayer).fadeOut();
  setTimeout(function () {
    $(target).hide();
  }, 300);
});

//-- (a fine elenco viene chiuso il menù ed il focus passa all'elemento successivo)
$(navlink).on('blur', function (event) {
  closemenu = $(this).closest('.navbar-collapsable').find('.close-div .btn'); // determino il pulsante di chiusura
  /* Act on the event */
  if ($(this).closest('.navbar-collapsable').hasClass('expanded')) { // se il navigatore è a perto
    setTimeout(function () {
      var active = document.activeElement; // determino quale elemento ha il focus
      var isMenu = $(active).closest('.navbar-collapsable').length; // controllo che l'elemento si trovi all'interno del navigatore

      if (isMenu == 0) { // se l'elemento è fuori dal navigatore
        $(closemenu).trigger('click') // simulo il click sul pulsante di chiusura del pannello
      }
    }, 50);
  }
});

//-- (a inizio elenco tabbando indietro, dopo il close chiudo il menu)
$(closebutton).on('blur', function (event) {
  closemenu = $(this);
  /* Act on the event */
  if ($(this).closest('.navbar-collapsable').hasClass('expanded')) { // se il navigatore è a perto
    setTimeout(function () {
      var active = document.activeElement; // determino quale elemento ha il focus
      var isMenu = $(active).closest('.navbar-collapsable').length; // controllo che l'elemento si trovi all'interno del navigatore

      if (isMenu == 0) { // se l'elemento è fuori dal navigatore
        $(closemenu).trigger('click') // simulo il click sul pulsante di chiusura del pannello
      }
    }, 50);
  }
});

